/**
 * const element = <h1 title="foo">Hello</h1>;
 * const container = document.getElementById('root');
 * ReactDOM.render(element, container);
 * ÂÜÖÈÉ®Ë∞ÉÁî® createElementÔºå‰º†ÈÄí tag nameÔºåprops Âíå children ÂèÇÊï∞
 */

export function createElement(type, props, ...children) {
    return {
        type,
        props: {
            ...props,
            // children ÊàñËÄÖ‰∏∫objectÔºåÂèØËÉΩÊòØstringÊàñËÄÖnumberÔºåÂõ†Ê≠§ÁâπÊÆäÂ§ÑÁêÜ‰∏∫ TEXT_ELEMENT
            children: children.map(child =>
                typeof child === "object"
                  ? child
                  : createTextElement(child)
            ),
        }
    };
}
/**
 * ‰∏∫child‰∏∫stringÂíånumberÁ±ªÂûãÁöÑÂéüÂßãÁ±ªÂûãÂàõÂª∫‰∏Ä‰∏™ÁâπÊÆäÁöÑÂÖÉÁ¥† TEXT_ELEMENT
 * @param {*} text ÊñáÊú¨ÂÜÖÂÆπ
 */
function createTextElement(text) {
    return {
        type: 'TEXT_ELEMENT',
        props: {
            nodeValue: text,
            children: []
        }
    };
}